import Fetch from 'isomorphic-unfetch';

class WeatherMap extends React.Component{

  state = {
    mapImage: undefined
  }

  getWeatherData = async() => {
    const res = await fetch('https://tile.openweathermap.org/map/clouds_new/1/1/1.png?appid=038b0cc9c6214552f03247312f5f166a');
    const response = await res;
    this.setState({
      mapImage:response
    });
    return '';
  }

  render(){
    return(
      <div>
        {this.getWeatherData()}
        <img src={this.state.mapImage} alt="Logo" />
      </div>
    );
  }
}

export default WeatherMap;
//https://tile.openweathermap.org/map/{layer}/{z}/{x}/{y}.png?appid={api_key}
